import { CommonModule, NgFor } from '@angular/common';
import { HttpClient } from '@angular/common/http';
import { Component } from '@angular/core';
import { FormsModule } from '@angular/forms';
import { ActivatedRoute } from '@angular/router';
import { environment } from '../../../../../environments/environment';

@Component({
  selector: 'app-readmore-blogs',
  standalone: true,
  imports: [CommonModule,FormsModule],
  templateUrl: './readmore-blogs.component.html',
  styleUrl: './readmore-blogs.component.scss'
})
export class ReadmoreBlogsComponent {

  mainBlog: any = null;
  latestBlogs: any[] = [];
  loading = true;

  constructor(private http: HttpClient, private route: ActivatedRoute) { }

  ngOnInit(): void {
    this.route.queryParamMap.subscribe(params => {
      const id = params.get('id');
      if (id) {
        this.fetchNews(id);
      }
    });
  }

  fetchNews(id: string) {
    this.http.get<any>(`${environment.apiUrl}/blogs`).subscribe({
      next: (res) => {
        if (res?.success && res.data?.data) {
          const allNews = res.data.data;

          this.mainBlog = allNews.find((n: any) => String(n.id) === id);

          this.latestBlogs = allNews
            .filter((n: any) => String(n.id) !== id)
            .slice(0, 3);
        }
        this.loading = false;
      },
      error: (err) => {
        console.error('Error fetching news:', err);
        this.loading = false;
      }
    });
  }

}
